    background-color: white;
}

.checkout__title {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}

.checkout__ad {
    width: 100%;
    height: 180px;
    margin-bottom: 10px;
}

.checkout__left {
    flex: 1;
}